.carousel {
  &__content-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 42.5px 40px;
    @include sp {
      flex-direction: column;
      padding: 21.25px 20px;
    }
  }
  
  &__photo {
    width: 37%;
    height: 290px;
    overflow: hidden;
    align-self: center;
    @include sp {
      width: 100%;
    }

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: 100% 45%;
    }
  }

  &__text-wrapper {
    display: flex;
    flex-direction: column;
    width: 60.55%;
    justify-content: center;
    @include sp {
      width: 100%;
    }
  }
  
  &__title {
    font-size: 2.4rem;
    line-height: getem(24, 34);
    letter-spacing: 0.04em;
    border-bottom: 1px solid $black;
    padding-bottom: 28.9597px;
    margin-bottom: 31.675px;
    @include sp {
      margin-top: 25px;
      font-size: 20px;
      padding-bottom: 17.38px;
      margin-bottom: 19px;
    }
  }
  
  &__line {
    border: none;
    background-color: $black;
    height: 1px;
  }
  
  &__text {
    font-size: 1.6rem;
    line-height: getem(16, 34);
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
    }
  }  
}
