.white {
  color: $white;
}

.white-bg {
  background: $white;
}

.black {
  color: $black;
}

.black-bg {
  background: $black;
}

.overflow {
  overflow: hidden;
}

.pc {
  display: block;
  @include sp {
    display: none;
  }
}

.sp {
  display: none;
  @include sp {
    display: block;
  }
}

.mw1200 {
  max-width: 1200px;
  width: 100%;
}

.mw1400 {
  max-width: 1400px;
}
