.template1 {
  .mwform-checkbox-field-text {
    position: relative;
    font-size: 1.8rem;
    font-weight: $medium;
    font-family: $noto-sans;
    letter-spacing: $ls50;
    padding-left: 46px;
    cursor: pointer;
    @include sp {
      font-size: getsp375(26px);
      padding-left: 25px;
    }
  
    &::before {
      content: '';
      width: 26px;
      height: 26px;
      border: 1px solid $gray5a;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      @include sp {
        width: getsp375(30px);
        height: getsp375(30px);
      }
    }
  
    &::after {
      content: '\2713';
      color: $white;
      display: none;
      position: absolute;
      left: 8px;
      top: 50%;
      transform: translateY(-50%);
      @include sp {
        left: 3px;
      }
    }
  }  
}
