.template1 {
  font-family: $shingo;
  font-weight: $regular;

  img {
    object-fit: cover;
  }

  &__hero-wrap {
    background: linear-gradient(rgba(0, 0, 0, 0.4)), url('../img/template1/house.jpg') no-repeat center;
    background-size: cover; 
    padding: 118.41px 5%;
    @include sp {
      padding: 71.05px 5%;
    }
  }

  &__hero {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    @include sp {
      height: max-content;
      flex-direction: column;
    }
  }

  &__hero-text-wrapper {
    display: inline-block;
    width: 45.71%;
    text-align: justify;
    color: $white;
    letter-spacing: 0.04em;
    margin: auto 0;
    @include sp {
      width: 100%;
    }
  }

  &__hero-title {
    font-size: 5rem;
    line-height: getem(50, 80);
    letter-spacing: 0.04em;
    filter: drop-shadow(3px 3px 3px rgba(0, 0, 0, 0.75));
    @include sp {
      font-size: 25px;
      text-align: center;
    }
  }

  &__hero-text {
    font-size: 2rem;
    line-height: getem(20, 44);
    letter-spacing: 0.04em;
    margin-top: 50.34px;
    @include sp {
      margin-top: 40px;
      font-size: 16px;
    }
  }

  &__article {
    display: flex;
    flex-direction: column;
    padding: 50px 5% 60px;
    align-items: center;
  }

  &__button {
    position: relative;
    width: 410px;
    height: 75px;
    background-color: $skyblue;
    font-size: 2.5rem;
    font-weight: $medium;
    border: none;
    border-radius: 3.75rem;
    color: $white;
    letter-spacing: 0.1em;
    cursor: pointer;
    margin: 0 auto;
    transition: 1s all ease;
    @include center;
    @include sp {
      width: 246px;
      height: 45px;
      background-color: $skyblue;
      font-size: 15.6px;
      font-weight: $medium;
      border: none;
      border-radius: 2.25rem;
    }

    &::before {
      position: absolute;
      content: '';
      height: 91px;
      width: 431px;
      border-radius: 4.72rem;
      border: 2px solid $skyblue;
      @include sp {
        height: 57px;
        width: 261px;
        border-radius: 2.83rem;
      }
    }

    &::after {
      position: absolute;
      content: '';
      background: url('../img/template1/svg/arrow-right.svg') no-repeat center;
      background-size: 100%;
      height: 34px;
      width: 34px;
      right: 25px;
      @include sp { 
        height: 20.4px;
        width: 20.4px;
        right: 15px;
      }
    }

    &:hover {
      background-color: #0095B7;

      &::before {
        border: 2px solid #0095B7;
      }
    }

    &:active {
      background-color: #006E87;

      &::before {
        border: 2px solid #006E87;
      }
    }
  }

  &__article-title {
    margin: 71.22px 0 38px;
    text-align: center;
    font-size: 3.5rem;
    letter-spacing: 0.04em;
    line-height: getem(35, 72);
    @include sp {
      font-size: 25px;
      margin: 44.05px 0 36px;
    }
  }

  &__article-content {
    display: flex;
    width: 100%;
    justify-content: space-between;
    @include sp {
      flex-direction: column;
    }
  }

  &__photo {
    width: 100%;
    height: 100%;
  
    &--hero {
      max-height: 543.18px;
      max-width: 690px;
      margin: auto 0;
      width: 49.29%;
      order: 2;
      @include sp {
        margin-top: 40px;
        order: 1;
        width: 100%;
      }
    }

    &--content {
      width: 45.71%;
      overflow: hidden;
      min-height: 576.8px;
      @include sp {
        min-height: auto;
        width: 100%;
      }

      img {
        object-position: 50% 50%;
      }
    }

    &--detail {
      height: 580px;
      min-height: 580px;
      width: 51.56%;
      align-self: center;
      overflow: hidden;
      @include sp {
        height: auto;
        min-height: max-content;
        width: 100%;
        margin-top: 25px;
      }

      img {
        object-position: 100% 0%;
      }
    }

    &--article {
      width: 37.5%;
      height: 320px;
      overflow: hidden;
      @include sp {
        width: 100%;
      }

      img {
        object-position: 100% 60%;
      }
    }

    &--voice {
      height: 380px;
      width: 48.06%;
      margin: auto 0;
      @include sp {
        height: max-content;
        width: 100%;
      }

      img {
        @include sp {
          margin-top: -20px;
          width: 100%;
          height: auto;
        }
      }
  
      &.sp {
        margin-top: 58.8px;
        @include sp {
          margin-top: 35.27px;
        }
      }
    }

    &--flow {
      min-width: 215px;
      width: 215px;
      height: 140px;
      border: 1px solid $darkgreen;
      @include center;
      @include sp {
        margin-top: 20px;
      }
    }

    &--laptop {
      max-height: 95px;
      max-width: 127px;
    }

    &--smartphone {
      max-height: 99px;
      max-width: 111px;
    }

    &--network {
      max-height: 111px;
      max-width: 111px;
    }

    &--applications {
      max-height: 105px;
      max-width: 141px;
    }
  }

  &__content-text-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 48.57%;
    @include sp {
      width: 100%;
      margin-top: 30px;
    }
  }

  &__content-title {
    font-size: 3.2rem;
    line-height: getem(32, 62);
    letter-spacing: 0.04em;
    border-bottom: 3px solid $skyblue;
    padding-bottom: 19.7px;
    margin-bottom: 37.7px;
    @include sp {
      text-align: center;
      font-size: 22px;
      padding-bottom: 23.4px;
      margin-bottom: 30px;
      text-align: left;
    }
  }

  &__content-title,
  &__content-text,
  &__heading {
    font-weight: $medium;
  }

  &__content-text {
    line-height: getem(18, 39);
    letter-spacing: 0.04em;
    justify-content: center;
  }

  &__content-text2 {
    margin: 14.89px 0 10.45px;
    letter-spacing: -0.01em;
    @include sp {
      margin: 25.64px 0 19.02px;
    }
  }

  &__content-text,
  &__content-text2 {
    font-size: 1.8rem;
    @include sp {
      font-size: 16px;
    }
  }

  &__content-number {
    font-size: 4.5rem;
    font-weight: $bold;
    margin: 0 -2px 0 3px;
    @include sp {
      font-size: 22.5px;
    }
  }

  &__content-small-text {
    font-size: 1.4rem;
    line-height: getem(14, 30);
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
    }
  }

  &__gallery {
    background-color: $lightblue;
    padding: 61.38px 0 127px;
    min-height: 680px;
    @include sp {
      padding: 61.38px 5% 127px;
    }
  }

  &__heading {
    position: relative;
    font-size: 3rem;
    font-weight: $medium;
    margin-bottom: 77.93px;
    text-align: center;
    min-width: 205px;
    letter-spacing: 0.04em;
    @include sp {
      font-size: 20px;
      font-weight: $bold;
    }

    &::after {
      content: '';
      position: absolute;
      height: 3px;
      width: 205px;
      background-color: $skyblue;
      bottom: -21.93px;
      left: 0;
      right: 0;
      margin: 0 auto;
    }

    &--problems {
      margin-bottom : 68.99px;
  
      &::after {
        width: 205.85px;
      }
    }

    &--gallery {
      margin-bottom: 70.22px;
  
      &::after {
        width: 204.23px;
      }
    }

    &--consulting {
      margin-bottom: 49.91px;
    }

    &--flow {
      margin-bottom: 56.84px;
    }

    &--points {
      margin-top: 133.57px;
      margin-bottom: 60.61px;
      @include sp {
        margin-top: 80.14px;
      }
    }

    &--voice {
      margin-bottom: 57.87px;
    }

    &--contact {
      margin-bottom: 58px;
    }
  
    &--question-answer {
      margin-bottom: 56.9px;
    }
  
    &--profile {
      color: $white;
      margin-bottom: 59.9px;
  
      &::after {
        background-color: $white;
        width: 100px;
      }
    }
  }

  &__provide {
    display: flex;
    flex-direction: column;
    padding: 80px 0;
    @include sp {
      padding: 48px 0;
    }
  }

  &__problems-wrap {
    width: 100%;
    padding: 0 5%;
    @include center;
  }

  &__problems {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 90.49px 50px 95px;
    width: 100%;
    min-height: 650px;
    border: 3px solid #bdccd4;
    @include sp {
      padding: 60px 25px 70px;
    }
  }

  &__triangle {
    position: absolute;
    content: '';
    background: url('../img/template1/svg/frame-down.svg') no-repeat center;
    background-size: 100%;
    background-color: white;
    height: 51px;
    width: 148px;
    bottom: -50.5px;
    left: 0;
    right: 0;
    margin: 0 auto;

    &::before {
      position: absolute;
      content: '';
      height: 3px;
      width: 4.5px;
      top: 0.5px;
      background-color: #bdccd4;
    }

    &::after {
      position: absolute;
      content: '';
      height: 3px;
      width: 4.5px;
      top: 0.5px;
      background-color: #bdccd4;
      right: 0;
    }
  }

  &__checklist {
    display: flex;
    flex-direction: column;
    min-height: 294px;
    @include mx980 {
      width: 100%;
    }
    @include sp {
      min-height: max-content;
    }
  }

  &__checklist-text {
    position: relative;
    font-size: 1.8rem;
    font-weight: $medium;
    letter-spacing: 0.04em;
    display: flex;
    justify-self: center;
    align-self: center;
    padding-left: 50.72px;
    @include sp {
      font-size: 16px;
    }

    &::before {
      content: '';
      position: absolute;
      height: 30px;
      width: 30px;
      min-width: 30px;
      border: 1px solid $black;
      top: 0;
      left: 0;
      @include sp {
        top: 0;
        bottom: 0;
        margin: auto 0;
      }
    }

    &::after {
      content: '';
      position: absolute;
      background: url("../img/template1/svg/check.svg") no-repeat center;
      height: 18.87px;
      width: 22.47px;
      top: 6px;
      left: 5px;
      @include sp {
        top: 0;
        bottom: 0;
        margin: auto 0;
      }
    }

    &:not(:first-child) {
      margin-top: 39px;
    }
  }

  &__points-wrap {
    width: 100%;
    @include center;
    @include sp {
      padding: 0 5%;
    }
  }

  &__points {
    max-width: 1920px;
    width: 100%;
  }

  &__points-list {
    list-style-type: none;
    counter-reset: gallery-cell;
  }

  &__points-detail {
    min-height: 580px;
    height: max-content;
    width: 100%;
    display: flex;
    counter-increment: gallery-cell;
    @include sp {
      flex-direction: column;
    }

    &:not(:first-child) {
      margin-top: 80px;
      @include sp {
        margin-top: 48px;
      }
    }

    &:nth-child(even) {
      .template1__photo--detail {
        order: 1;
        margin-right: 40.53px;
        @include sp {
          order: 2;
          margin-right: 0;
        }
      }
  
      .template1__detail-article {
        order: 2;
        padding: 0 5% 0 0;
        margin-right: 0;
        @include sp {
          order: 1;
          padding-right: 0;
        }
      }
  
      .template1__detail-text-wrapper {
        margin: auto 0 0 0;
      }
    }
  }

  &__detail-article {
    width: 46.35%;
    display: flex;
    padding-left: 5%;
    margin-right: 40.53px;
    @include sp {
      padding-left: 0;
      width: 100%;
      margin-right: 0;
    }
  }

  &__detail-text-wrapper {
    width: 630px;
    height: 100%;
    margin: 0 0 0 auto;
    @include sp {
      width: 100%;
    }
  }

  &__detail-point {
    font-size: 2.5rem;
    font-family: $brandon;
    font-weight: $bold;
    margin-top: 7px;
    @include sp {
      font-size: 20px;
    }
  }

  &__detail-number {
    &::after {
      margin-left: -7px;
      font-size: 9rem;
      line-height: 9rem;
      content: counter(gallery-cell, decimal-leading-zero);
      @include sp {
        font-size: 45px;
        margin-left: 0;
      }
    }
  }

  &__detail-title {
    font-size: 3.2rem;
    font-weight: $medium;
    line-height: getem(32, 62);
    margin-top: 23.78px;
    letter-spacing: 0.04em;
    @include sp {
      margin-top: 0;
      font-size: 25px;
    }
  }

  &__detail-text {
    font-size: 1.8rem;
    line-height: getem(18, 39);
    text-align: justify;
    margin-top: 34.62px;
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
      margin-top: 0;
    }
  }

  &__article-short {
    background-color: $lightblue;
    display: flex;
    flex-direction: column;
    padding: 77.5px 5%;
    align-items: center;
    @include sp {
      padding: 46.5px 5%;
    }
  }

  &__article-detail {
    display: flex;
    margin-bottom: 68px;
    @include sp {
      flex-direction: column;
      margin-bottom: 40.8px;
    }
  }

  &__article-text-wrapper {
    width: 62.5%;
    background-color: $white;
    display: flex;
    align-items: center;
    padding: 25px;
    @include sp {
      width: 100%;
    }
  }

  &__article-text  {
    font-size: 1.64rem;
    line-height: getem(16.4, 34.87);
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
    }
  }

  &__consulting {
    padding: 68px 5% 80px;
    flex-direction: column;
    @include center;
    @include sp {
      padding: 48px 5%;
    }
  }

  &__consulting-text {
    font-size: 1.8rem;
    line-height: getem(18, 39);
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
    }
  }

  &__table-wrapper {
    margin: 27.41px 0 69.75px 0;
    @include sp {
      margin: 24px 0 48px 0;
    }
  }

  &__table {
    width: 100%;
    border-collapse: collapse;
    font-size: 2.4rem;
    letter-spacing: 0.04em;
    text-align: center;
    @include sp {
      font-size: 12px;
    }
  }

  &__table-box {
    height: 80px;
    width: 25%;
    border: 2px solid $lightgrey;

    &--none {
      border: white;
    }

    &--row-label {
      color: $white;
      background-color: $skyblue;
    }
  }

  tbody &__table-horizontal {
    &:nth-child(odd) {
      background-color: $lightblue;
    }
  }

  &__circle {
    background: url("../img/template1/svg/circle.svg") no-repeat center;
    background-size: 34px;
    @include sp {
      background-size: 20.4px; 
    }
  }

  &__circle-double {
    background: url("../img/template1/svg/double-circle.svg") no-repeat center;
    background-size: 34px;
    @include sp {
      background-size: 20.4px; 
    }
  }

  &__cross {
    background: url('../img/template1/svg/cross.svg') no-repeat center;
    background-size: 36.12px;
    @include sp {
      background-size: 21.67px; 
    }
  }
  
  &__flow {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    counter-reset: flow-number;
    margin-bottom: 70px;
    @include sp {
      margin-bottom: 48px;
    }
  }

  &__flow-detail {
    position: relative;
    height: max-content;
    border: 5px solid $lightblue2;
    border-radius: 8px;
    padding: 39px 95.89px 33px;
    min-height: 270px;
    width: 100%;
    counter-increment: flow-number;
    justify-content: center;
    @include center;
    @include sp {
      padding: 25px;
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 96px;
      @include sp {
        margin-bottom: 49px;
      }

      &::after {
        position: absolute;
        content: '';
        background: url('../img/template1/svg/pointer-down.svg') no-repeat center;
        background-size: 100%;
        height: 36px;
        width: 96px;
        bottom: -70px;
        left: 0;
        right: 0;
        margin: 0 auto;
        @include sp {
          height: 21.6px;
          width: 57.6px;
          bottom: -40px; 
        }
      }
    }
  }

  &__flow-number {
    position: relative;
    font-family: $brandon;
    font-size: 6rem;
    line-height: 7rem;
    height: 70px;
    width: 70px;
    min-width: 70px;
    border: 2px solid $skyblue;
    border-radius: 50%;
    align-self: flex-start;
    @include sp {
      font-size: 50px;
      min-height: 42px;
      min-width: 42px;
      align-self: center;
    }

    &::after {
      position: absolute;
      height: 100%;
      width: 100%;
      text-align: center;
      top: -2px;
      content: counter(flow-number);
      color: $skyblue;
    }
  }

  &__flow-text-wrapper {
    width: 100%;
    margin: 0 23.11px 0 19.61px;
    @include sp {
      margin: 0;
    }
  }

  &__flow-title {
    font-size: 2.5rem;
    line-height: getem(25, 56);
    font-weight: $medium;
    margin-top: 6.67px;
    margin-bottom: 25.11px;
    letter-spacing: 0.04em;
    @include sp {
      font-size: 20px;
      text-align: center;
      margin: 20px 0;
    }
  }

  &__flow-text {
    text-align: justify;
    font-size: 1.8rem;
    line-height: getem(18, 39);
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
    }
  }

  &__voice {
    max-width: 1340px;
    width: 100%;
  }

  &__voice-detail {
    width: 100%;
    background-color: $lightgrey2;
    min-height: 480px;
    height: max-content;
    display: flex;
    border-radius: 16px;
    overflow: hidden;
    padding: 50px 0;
    @include sp {
      padding: 25px 0;
      flex-direction: column;
    }

    &:not(:first-child) {
      margin-top: 59px;
      @include sp {
        margin-top: 30px;
      }
    }

    &:nth-child(even) {
      .template1__voice-text-wrapper {
        order: 2;
      }
      
      .template1__photo--voice {
        order: 1;
      }
    }
  }

  &__voice-text-wrapper {
    width: 51.94%;
    padding: 0 83.45px 0 81.89px;
    margin: auto 0;
    @include mx980 {
      padding: 0 40px;
    }
    @include sp {
      padding: 0 25px;
      width: 100%;
    }
  }

  &__voice-title {
    font-size: 2.4rem;
    line-height: getem(24, 40);
    letter-spacing: 0.1em;
    @include sp {
      font-size: 22px;
    }
  }

  &__voice-text {
    margin-top: 38.79px;
    font-size: 1.64rem;
    line-height: getem(16.4, 34.87);
    text-align: justify;
    letter-spacing: 0.04em;
    @include sp {
      font-size: 16px;
      margin-top: 15px;
    }
  }

  &__question-answer-wrap {
    background-color: $lightblue;
    padding: 0 5%;
  }

  &__question-answer {
    padding: 96px 0 106.5px;
    max-width: 1200px;
    margin: 0 auto;
    @include sp {
      padding: 45px 0;
    }
  }

  &__qa {
    padding: 34.17px 49.88px 26.73px;
    background-color: $white;
    @include sp {
      padding: 20px;
    }

    &:not(:first-child) {
      margin-top: 40px;
      @include sp {
        margin-top: 20px;
      }
    }
  }

  &__qa-text {
    font-size: 1.8rem;
    font-weight: $medium;
    line-height: getem(18, 32);
    letter-spacing: 0.05em;
    display: flex;
    @include sp {
      font-size: 16px;
    }

    &--border-bottom {
      border-bottom: 2px solid $black;
      padding-bottom: 20px;
      margin-bottom: 20px;
    }

    &--regular {
      margin-top: 23.803px;
      font-weight: $regular;
      text-align: justify;
      @include sp {
        margin-top: 20px;
      }
    }
  }

  &__qa-icon {
    position: relative;
    height: 34px;
    width: 34px;
    margin-right: 26.43px;
    @include sp {
      margin-right: 20px;
    }

    &--text {
      &::after {
        position: absolute;
        content: attr(data-text);
        font-size: 2rem;
        font-family: $brandon;
        font-weight: $bold;
        color: white;
        width: 100%;
        top: 0;
        text-align: center; 
        @include sp {
          font-size: 20px;
          padding-top: 2px;
        }
      }
    }
  }

  &__contact {
    padding: 66px 5% 91px;
    @include sp {
      padding: 33px 5% 50px;
    }
  }

  &__form {
    margin: 0 auto;
  }

  &__form-item {
    width: 100%;
    background-color: $lightblue;
    padding: 25px 100px;
    display: grid;
    grid-template-columns: 251.55px auto;
    font-weight: $medium;
    min-height: 116px;
    height: max-content;
    align-items: center;
    @include sp {
      display: flex;
      flex-direction: column;
      padding: 25px;
    }

    &:not(:first-child) {
      margin-top: 30px;
    }
  }

  &__form-label {
    font-size: 2rem;
    display: flex;
    justify-content: space-between;
    align-self: center;
    letter-spacing: 0.02em;
    @include sp {
      width: 100%;
      font-size: 16px;
    }

    span {
      padding: 7.72px 14px;
      border-radius: 5px;
      background-color: $skyblue;
      color: $white;
      font-weight: $bold;
      font-size: 1.6rem;
      line-height: 1.6rem;
      @include sp {
        font-size: 16px;
      }
    }
  }

  &__form-input-wrap {
    margin-left: 38.7px;
    width: 100%;
    @include sp {
      margin: 25px 0 0 0;
      width: 100%;
    }
  }

  &__form-input {
    input[type="text"],
    textarea {
      font-family: $shingo;
      line-height: 1.75em;
      border: 1px solid $darkgreen;
      width: 100%;
      padding: 14.43px 12px 12px;
      font-size: 1.8rem;
      letter-spacing: 0.02em;
      outline: none;
      @include sp {
        font-size: 16px;
        line-height: getem(26, 42);
        padding: getsp375(22px) getsp375(30px);
      }
    }

    input[type="text"] {
      min-width: 400px;
      width: 40%;
      height: 45px;
      @include mx980 {
        min-width: 100%;
      }
      @include sp {
        min-width: 100%;
        height: getsp375(81px);
      }
    }

    textarea {
      height: 220px;
      line-height: getem(18, 36);
      resize: none;
      @include sp {
        height: getsp375(220px);
      }
    }
  }

  &__form-helper {
    margin-top: 12px;
    font-size: 1.5rem;
    @include sp {
      font-size: 15px;
    }
  }

  &__form-privacy {
    margin: 40px auto 50px;
    height: max-content;
    position: relative;
    padding-left: 55px;
    font-size: 1.8rem;
    letter-spacing: 0.05em;
    width: 100%;
    @include center;
    @include sp {
      font-size: 18px;
    }

    span {
      cursor: pointer;
    }
  }

  &__form-checkbox {
    position: relative;

    input[type="checkbox"] {
      outline: none;
      box-shadow: none;
      appearance: none;
      position: absolute;
      top: 0;
      left: -47.94px;
      cursor: pointer;
    
      &::before {
        content: '';
        width: 26px;
        height: 26px;
        border: 1px solid $darkgreen;
        background-color: $white;
        outline: 3 solid $white;
        outline-offset: -5px;
        position: absolute;
        top: 0;
        left: 0;
      }
  
      &::after {
        content: '';
        width: 5px;
        height: 10px;
        border: solid $skyblue;
        border-width: 0 3px 3px 0;
        position: absolute;
        left: 7px;
        top: 2px;
        opacity: 0;
        visibility: hidden;
        transition: 0.2s ease;
        transform: rotate(45deg);
        @include sp {
          left: 7px;
          top: 2px;
          transform: rotate(45deg);
        }
      }
    
      &:checked {
        &::after {
          opacity: 1;
          visibility: visible;
          width: 6.5px;
          height: 13px;
          margin: 2px 0 0 2px;
        }
      }
    }

    span {
      font-size: 1.8rem;      
      font-weight: $medium;
      letter-spacing: 0.05em;
      @include sp {
        font-size: 18px;
      }
    }
  }

  &__profile {
    background-color: $skyblue;
    width: 100%;
    padding: 53px 5% 61.5px;
    flex-direction: column;
    @include center;
    @include sp {
      padding: 36.9px 5%;
    }
  }

  &__profile-content {
    max-width: 1000px;
    margin: 0 auto;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include sp {
      flex-direction: column;
    }
  }

  &__profile-col {
    width: 50%;
    margin: auto 0;
    @include sp {
      width: 100%;
    }

    &:not(:last-child) {
      width: 48%;
      @include sp {
        width: 100%;
        margin-bottom: 25px;
      }
    }
  }

  &__profile-item {
    display: flex;
    align-items: flex-start;
    width: 100%;

    &:not(:first-child) {
      margin-top: 18.4px;
    }

    &:not(:last-child) {
      margin-bottom: getpc(45px);
      @include sp {
        margin-bottom: getps375(50px);
      }
    }
  }

  &__profile-text {
    color: $white;
    font-size: 1.8rem;
    letter-spacing: 0.05em;
    @include sp {
      font-size: 16px;
    }

    &--label {
      margin-right: 40px;
      width: 25%;
      min-width: 90px;
      font-weight: $bold;
      @include sp {
        margin-right: getsp375(30px);
      }
    }
  
    &--description {
      width: 75%;
    }
  }

  &__profile-iframe {
    margin: auto;

    iframe {
      min-height: 280px;
      @include sp {
        width: 100%;
        height: auto;
      }
    }
  }

  &__footer-cr {
    background-color: $lightblue;
    padding: 6px 5%;
    height: 40px;
    @include center;
  }

  &__footer-cr-text {
    font-size: 1.2rem;
    font-weight: $medium;
    letter-spacing: 0.05em;
    @include sp {
      font-size: 9px;
    }
  }
}
