.splide {
  position: relative;
  max-width: 100%;
  height: max-content !important;
  width: 100%;
  margin: 0 auto;
  @include mx1333 {
    padding: 0 12%;
  }
  @include sp {
    padding: 0;
  }

  &__slide {
    max-width: 1170px;
    width: 80%;
    min-height: 375px;
    height: max-content;
    margin-right: 165px;
    background: $white;
    border: 1px solid $black;

    img {
      width: 100%;
    }
  }

  &__arrow {
    svg {
      display: none;
    }

    &--prev {
      background: url('../img/template1/svg/arrow-next.svg') no-repeat center;
      transform: rotate(180deg);
      background-size: 60px 60px;
      height: 60px;
      width: 60px;
      background-color: white;
      margin-left: 50%;
      left: -690px;
      top: 157.5px;
      opacity: 1;
      @include mx1400 {
        left: 20px;
        margin-left: 0;
      }
      @include sp {
        top: auto;
        bottom: -83px;
        background-size: 36px 36px;
        height: 36px;
        width: 36px;
        left: 0;
      }
    }

    &--next {
      background: url('../img/template1/svg/arrow-next.svg') no-repeat center;
      background-size: 60px;
      height: 60px;
      width: 60px;
      background-color: white;
      right: -700px;
      margin-right: 50%;
      top: 187.5px;
      opacity: 1;
      @include mx1400 {
        right: 20px;
        margin-right: 0;
      }
      @include sp {
        top: auto;
        bottom: -102px;
        background-size: 36px 36px;
        height: 36px;
        width: 36px;
        right: 0;
      }
    }
  }

  &__pagination {
    margin: 0 20px;
    
    &--ltr {
      position: absolute;
      bottom: -56px;
      @include sp {
        bottom: -75px;
      }
    }
  }

  &__pagination__page {
    background-color: #bdcbd4;
    opacity: 1;
    height: 16px;
    width: 16px;
    margin: 13px 13px 0 13px;

    &.is-active {
      background-color: $skyblue;
      transform: none;
    }
  }
}
