.template-2 {
  font-family: $noto-sans;
  color: $black001;

  &__button {
    position: relative;
    width: 100%;
    height: 90px;
    border-radius: 8px;
    background-color: $green;
    font-size: 3rem;
    font-family: $noto-sans;
    font-weight: $bold;
    letter-spacing: $ls40;
    color: $white;
    border: 2px solid $green;
    transition: background-color 0.3s ease;
    cursor: pointer;
    @include center;
    @include sp {
      height: getsp375(120px);
      font-size: getsp375(40px);
    }

    svg {
      width: 33px;
      height: 11px;
      fill: $white;
      position: absolute;
      right: 24px;
      @include sp {
        width: 25px;
        right: 20px;
      }
    }

    &:hover {
      background-color: $white;
      color: $green;

      svg {
        fill: $green;
      }
    }
  }

  &__title-container {
    display: flex;
    align-items: center;

    &--center {
      justify-content: center;
      @include sp {
        justify-content: flex-start;
      }
    }
  }

  &__title {
    padding: 6px 22px;
    background-color: $green;
    font-size: 3rem;
    letter-spacing: $ls40;
    color: $white;
    @include sp {
      font-size: getsp375(40px);
      padding: 10px 20px 11px;
    }

    &--white {
      background-color: $white;
      color: $green;
      @include sp {
        margin: 0 5%;
      }
    }
  }

  &__description {
    font-size: 1.8rem;
    font-weight: $medium;
    letter-spacing: $ls40;
    line-height: getem(18px, 36px);
    margin-top: 30px;
    text-align: justify;
    @include sp {
      font-size: getsp375(30px);
    }
  } 

  &__mv {
    max-width: 1920px;
    width: 100%;
    max-height: 934px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    @include sp {
      max-width: 100%;
      flex-direction: column-reverse;
      align-items: center;
      padding-bottom: 20px;
    }
  }

  &__mv-image-container {
    width: 68%;
    @include sp {
      width: 100%;
    }
  }

  &__image,
  &__flow-item-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__mv-details {
    width: 620px;
    padding: 0 49px;
    background-color: $white;
    flex-direction: column;
    @include center;
    @include sp {
      width: 100%;
      padding: 0 5%;
    }
  }

  &__mv-title {
    font-size: 4.3rem;
    line-height: getem(43px, 74px);
    letter-spacing: $ls70;
    color: $green;
    padding-bottom: 98px;
    text-align: justify;
    @include sp {
      font-size: getsp375(52px);
      padding: 20px 0;
    }
  }

  &__mv-subtitle {
    font-size: 2rem;
    font-weight: $medium;
    letter-spacing: $ls70;
    text-align: center;
    padding-bottom: 20px;
  }

  &__heading {
    position: relative;
    padding: 120px 0;
    background-color: $lightgreen;
    @include sp {
      padding: getsp375(120px) 0;
    }
  }

  &__heading-wrapper {
    max-width: 1436px;
    width: 100%;
    height: 580px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @include sp {
      max-width: 100%;
      height: auto;
      flex-direction: column;
      padding: 0 5%;
      gap: 20px;
    }
  }

  &__heading-image-container {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 52%;
    height: 580px;
    @include sp {
      position: initial;
      width: 100%;
      height: 300px;
      transform: none;
    }
  }

  &__heading-details {
    width: 44%;
    @include mx1500 {
      padding-right: 5%;
    }
    @include sp {
      width: 100%;
      padding-right: 0;
    }
  }

  &__heading-title {
    font-size: 3.2rem;
    line-height: getem(32px, 62px);
    letter-spacing: $ls40;
    color: $green;
    padding-bottom: 30px;
    text-align: justify;
    @include sp {
      font-size: getsp375(44px);
      padding-bottom: getsp375(30px);
    }
  }

  &__heading-text {
    font-size: 1.8rem;
    font-weight: $medium;
    color: $black1a;
    line-height: getem(18px, 39px);
    letter-spacing: $ls40;
    text-align: justify;
    @include sp {
      font-size: getsp375(30px);
    }
  }

  &__features {
    background-color: $off-white;
    padding: 120px 0;
    @include sp {
      padding: getsp375(80px) 0 getsp375(80px);
    }
  }

  &__features-title {
    @include center;
  }

  &__features-list {
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 70px;
    gap: 51px;
    @include center;
    @include mx1200 {
      padding: 70px 5% 0;
    }
    @include sp {
      max-width: 100%;
      flex-direction: column;
      padding-top: getsp375(80px);
      gap: getsp375(70px);
    }
  }

  &__features-item {
    position: relative;
    width: 366px;
    padding-top: 26px;
    @include sp {
      width: 280px;
    }
  }

  &__features-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 73px 45px 28px;
    border: 5px solid $black001;
    border-radius: 10px;
    @include sp {
      height: auto;
      background-size: contain;
      gap: getsp375(40px);
      padding: getsp375(80px) getsp375(70px) getsp375(30px);
    }
  }

  &__features-top-border {
    position: relative;
    width: 220px;
    height: 5px;
    background-color: $off-white;
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 0;
    @include sp {
      width: 180px;
    }

    &::before,
    &::after {
      content: '';
      width: 20px;
      height: 4.9px;
      border: 10px;
      background-color: $black;
      position: absolute;
      top: 0;
      z-index: 1;
    }

    &::before { 
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      left: 0;
    }

    &::after {
      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
      right: 0;
    }
  }

  &__features-icon-container {
    width: 180px;
    height: 180px;
    background-color: $green80;
    border-radius: 100%;
    @include center;
    @include sp {
      width: getsp375(240px);
      height: getsp375(240px);
    }
  }

  &__features-icon {
    width: 100%;
    height: 110px;
    @include sp {
      height: getsp375(150px);
    }
  }

  &__features-text {
    font-size: 2rem;
    font-weight: $bold;
    font-family: $yugo;
    line-height: getem(20px, 37px);
    text-align: center;
    @include sp {
      font-size: getsp375(32px);
    }
  }

  &__features-title-container {
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__features-item-title {
    font-size: 2.4rem;
    font-weight: $bold;
    font-family: $bahnschrift;
    line-height: getem(70px, 37px);
    @include sp {
      font-size: getsp375(36px);
    }

    &--big {
      font-size: 7rem;
      @include sp {
        font-size: getsp375(100px);
      }
    }
  }

  &__features-details-list {
    width: 100%;
    margin-top: 90px;
    display: flex;
    flex-direction: column;
    gap: 100px;
    align-items: flex-start;
    @include sp {
      gap: getsp375(100px);
      margin-top: getsp375(100px);
    }
  }

  &__features-details-item {
    position: relative;
    width: 81.25%;
    background-color: $white;
    display: flex;
    justify-content: flex-end;
    filter: drop-shadow(3px 3px 10px rgba(0, 0, 0, 0.2));
    @include sp {
      width: 100%;
      flex-direction: column;
      height: auto;
    }

    &:nth-of-type(even) {
      align-self: flex-end;
      flex-direction: row-reverse;
      @include sp {
        align-self: inherit;
        flex-direction: inherit;
      }
    }
  }

  &__features-details-image-container {
    width: 39.10%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    @include sp {
      position: initial;
      width: 100%;
      height: 300px;
    }

    &--right-aligned {
      left: unset;
      right: 0;
    }
  }

  &__features-details-content {
    width: 61%;
    padding: 95px 60px 86px;
    @include sp {
      width: 100%;
      padding: getsp375(100px) 5%;
    }
  }

  &__features-details-title-container {
    display: flex;
    align-items: flex-end;
    gap: 60px;
    @include sp {
      align-items: center;
      gap: getsp375(60px);
    }

    &--small-gap {
      gap: 37px;
    }
  } 

  &__features-details-subtitle {
    font-size: 14rem;
    font-weight: $bold;
    font-family: $bahnschrift;
    color: $greenb2;
    height: 140px;
    @include sp {
      font-size: 100px;
      height: 100px;
    }
  }

  &__features-details-title {
    font-size: 3rem;
    font-weight: $bold;
    font-family: $noto-sans;
    color: $green;
    line-height: getem(30px, 62px);
    text-align: justify;
    @include sp {
      font-size: getsp375(40px);
    }
  }

  &__features-details-description {
    font-size: 1.8rem;
    font-weight: $medium;
    font-family: $noto-sans;
    line-height: getem(18px, 39px);
    letter-spacing: $ls40;
    padding-top: 55px;
    text-align: justify;
    @include sp {
      font-size: getsp375(30px);
      padding-top: getsp375(50px);
    }
  }

  &__inquiry {
    max-width: 900px;
    margin: 0 auto;
    height: 274px;
    background-color: $green80;
    border-radius: 10px;
    padding: 9px;
    margin-top: 94px;
    @include sp {
      height: 310px;
      margin: 120px 5% 0;
    }
  }

  &__inquiry-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: $white;
    border-radius: inherit;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 59px;
    @include sp {
      padding: 0 5% 20px;
    }
  }

  &__inquiry-image-container {
    width: 300px;
    height: 342px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 40px;
    @include sp {
      width: getsp375(400px);
      height: getsp375(442px);
      top: 0;
      left: 50%;
      transform: translate(-50%, -40%);
    }
  }

  &__inquiry-image {
    width: 100%;
    height: 100%;
  }

  &__inquiry-details {
    width: 440px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 21px;
    @include sp {
      width: 100%;
      height: 100%;
      justify-content: flex-end;
    }
  }

  &__inquiry-title {
    font-size: 3.2rem;
    font-family: $noto-sans;
    letter-spacing: 0.07em;
    @include sp {
      font-size: getsp375(48px);
    }
  }

  &__voice {
    width: 100%;
    background-color: $white;
    padding: 63px 0 40px;
    @include sp {
      padding: getsp375(80px) 0;
    }
  }

  &__voice-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    @include mx1200 {
      padding: 0 5%;
    }
  }

  &__voice-list {
    display: flex;
    flex-direction: column;
    padding-top: 48px;
    gap: 60px;
    @include sp {
      padding-top: getsp375(50px);
      gap: getsp375(80px);
    }
  }

  &__voice-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    @include sp {
      flex-direction: column;
      gap: 30px;
    }
  }

  &__voice-image-container {
    width: 180px;
    height: 180px;
    border-radius: 100%;
    overflow: hidden;
    @include sp {
      width: 120px;
      height: 120px;
    }
  }

  &__voice-item-wrapper {
    position: relative;
    width: 85.3%;
    padding-left: 40px;
    @include sp {
      width: 100%;
      padding-left: 0;
      padding-top: 21px;
    }
  }

  &__voice-container {
    padding: 40px 50px;
    border: 4px solid $greenb2;
    border-radius: 10px;
    @include sp {
      padding: getsp375(40px) 5%;
    }

    &::before {
      content: '';
      width: 50px;
      height: 50px;
      border-bottom: 4px solid $greenb2;
      border-left: 4px solid $greenb2;
      background-color: $white;
      transform: rotate(45deg) translateY(-50%);
      position: absolute;
      left: -5px;
      top: 50%;
      @include sp {
        width: getsp375(80px);
        height: getsp375(80px);
        left: 50%;
        top: 0;
        transform: translateX(-50%) rotate(135deg);
      }
    }
  }

  &__voice-title {
    position: relative;
    font-size: 2.3rem;
    font-family: $noto-sans;
    letter-spacing: $ls40;
    color: $green;
    padding-left: 15px;
    @include sp {
      font-size: getsp375(36px);
    }

    &::before {
      content: '';
      width: 3px;
      height: 25px;
      background-color: $green;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__voice-content {
    font-size: 1.8rem;
    font-weight: $medium;
    font-family: $noto-sans;
    letter-spacing: $ls40;
    line-height: getem(18px, 36px);
    text-align: justify;
    margin-top: 15px;
    @include sp {
      font-size: getsp375(30px);
      margin-top: getsp375(20px);
    }
  }

  &__additional {
    padding: 75px 0 76px;
    background-color: $lightgreen;
    @include sp {
      padding: getsp375(80px) 0;
    }
  }

  &__additional-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    @include mx1200 {
      padding: 0 5%;
    }
  }

  &__additional-title {
    position: relative;
    font-size: 2.4rem;
    letter-spacing: $ls40;
    line-height: getem(24px, 38px);
    text-align: justify;
    padding-bottom: 14px;
    @include sp {
      font-size: getsp375(36px);
    }

    &::after {
      content: '';
      width: 100%;
      height: 2px;
      background-color: $green;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  &__additional-content {
    font-size: 1.8rem;
    font-weight: $medium;
    font-family: $noto-sans;
    letter-spacing: $ls40;
    line-height: getem(18px, 36px);
    text-align: justify;
    margin-top: 20px;
    @include sp {
      font-size: getsp375(30px);
    }
  }

  &__flow {
    background-color: $lightgreen;
    padding: 96px 0;
    margin-top: 96px;
    @include sp {
      margin-top: getsp375(96px);
      padding: getsp375(80px) 0;
    }
  }

  &__flow-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    @include mx1200 {
      padding: 0 5%;
    }
  }

  &__flow-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 63px;
    margin-top: 62px;
    @include sp {
      gap: getsp375(80px);
    }
  }

  &__flow-item {
    display: flex;
    flex-direction: column;
    width: 358px;
  }

  &__flow-title-container {
    display: flex;
    align-items: center;
    gap: 46px;
    @include sp {
      gap: getsp375(50px);
    }

    &:hover {
      & ~ {
        .template-2__flow-item-image-container {
          .template-2__flow-item-image {
            transform: scale(1.2);
          }
        }
      }
    }
  }

  &__flow-item-subtitle {
    font-size: 7rem;
    font-weight: $bold;
    font-family: $bahnschrift;
    line-height: getem(70px, 37px);
    color: $green;
  }

  &__flow-item-title {
    font-size: 1.8rem;
    font-weight: $bold;
    font-family: $noto-sans;
    letter-spacing: $ls40;
    line-height: getem(18px, 32px);
    color: $black;
    margin-top: -11px;
    text-align: justify;
    @include sp {
      font-size: getsp375(30px);
    }
  }

  &__flow-item-image-container {
    width: 100%;
    height: 198px;
    margin-top: 15px;
    overflow: hidden;

    &:hover {
      .template-2__flow-item-image {
        transform: scale(1.1);
      }
    }
  }

  &__flow-item-image {
    transition: 0.3s ease;
  }

  &__flow-item-description {
    font-size: 1.6rem;
    font-weight: $medium;
    font-family: $noto-sans;
    letter-spacing: $ls40;
    line-height: getem(16px, 36px);
    text-align: justify;
    margin-top: 20px;
    @include sp {
      font-size: getsp375(24px);
    }
  }

  &__faq {
    padding: 93px 0;
    @include sp {
      padding: getsp375(80px) 0;
    }
  }

  &__faq-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    @include mx1200 {
      padding: 0 5%;
    }
  }

  &__faq-list {
    display: flex;
    flex-direction: column;
    gap: 60px;
    margin-top: 60px;
    overflow: hidden;
    @include sp {
      gap: getsp375(60px);
      margin-top: getsp375(70px);
    }
  }

  &__faq-item {
    position: relative;
    animation-delay: 0.5s;
  }

  &__faq-checkbox {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    z-index: 1;

    &:checked {
      & ~ {
        .template-2__faq-answers-container {
          margin-top: 0;
          max-height: 0;
          opacity: 0;
          transform: translate(0, 50%);
        }

        .template-2__faq-questions-container {
          .template-2__faq-arrow {
            &::before {
              transform: translate(-50%, -50%) rotate(360deg);
            }
          }
        }
      }
    }
  }

  &__faq-questions-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    padding-left: 80px;
    @include sp {
      height: auto;
      padding-left: getsp375(100px);
      gap: 5%;
      align-items: flex-start;
    }
  }

  &__faq-question {
    font-size: 2rem;
    font-weight: $bold;
    letter-spacing: $ls40;
    @include sp {
      width: 70%;
      font-size: getsp375(32px);
    }

    &::before {
      content: '';
      width: 50px;
      height: 50px;
      background-image: url('../img/template-2/svg/faq-questions-icon.svg');
      background-size: contain;
      background-repeat: no-repeat;
      font-size: 2.5rem;
      font-weight: $bold;
      font-family: $bahnschrift;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      @include sp {
        width: getsp375(70px);
        height: getsp375(70px);
        top: 0;
        transform: translateY(0);
      }
    }
  }

  &__faq-arrow {
    width: 50px;
    height: 50px;
    border: 2px solid $green;
    position: relative;
    @include sp {
      width: 35px;
      height: 35px;
    }

    &::before {
      content: '';
      width: 17px;
      height: 12px;
      background-image: url('../img/template-2/svg/faq-arrow.svg');
      background-size: contain;
      background-repeat: no-repeat;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) rotate(180deg);
      transition: 0.3s ease;
      @include sp {
        width: 13px;
        height: 8px;
      }
    }
  }

  &__faq-answers-container {
    max-height: 100%;
    padding-left: 80px;
    margin-top: 20px;
    opacity: 1;
    transition: 0.2s ease-in-out;
    @include sp {
      padding: 0;
    }
  }

  &__faq-answers {
    position: relative;
    padding: 17px 30px 11px 110px;
    background-color: $lightgray;
    font-size: 1.8rem;
    font-weight: $bold;
    font-family: $shinGo;
    line-height: getem(18px, 39px);
    letter-spacing: $ls40;
    transition: 0.3s ease-in-out;
    @include sp {
      padding: 5% 5% 5% 80px;
      font-size: getsp375(30px);
    }

    &::before {
      content: '';
      width: 50px;
      height: 50px;
      background-image: url('../img/template-2/svg/faq-answers-icon.svg');
      background-size: contain;
      background-repeat: no-repeat;
      position: absolute;
      left: 30px;
      top: 29px;
      @include sp {
        width: getsp375(70px);
        height: getsp375(70px);
        left: 20px;
      }
    }
  }

  &__contact {
    background-color: $off-white;
    padding: 40px 0 80px;
    @include mx1200 {
      padding: 40px 5% 80px;
    }
    @include sp {
      padding: getsp375(80px) 5%;
    }
  }

  &__contact-wrapper {
    max-width: 1200px;
    margin: 40px auto 0;
    background-color: $white;
    padding: 49px 126px 50px 74px;
    @include sp {
      max-width: 100%;
      padding: 5%;
      margin: getsp375(50px) auto 0;
    }
  }

  &__contact-list {
    display: flex;
    flex-direction: column;
    gap: 40px;
    @include sp {
      gap: getsp375(60px);
    }
  }

  &__contact-item {
    display: flex;
    align-items: center;
    gap: 40px;
    @include sp {
      flex-direction: column;
      align-items: flex-start;
      gap: getsp375(40px);
    }

    &--padded {
      position: relative;
      padding-bottom: 27px;
    }

    &--checkbox {
      justify-content: center;
      @include sp {
        align-items: center;
      }
    }
  }

  &__contact-title {
    position: relative;
    font-size: 2rem;
    font-weight: $medium;
    font-family: $noto-sans;
    line-height: getem(20px, 24px);
    letter-spacing: $ls20;
    width: 25%;
    @include sp {
      width: 100%;
      font-size: getsp375(32px);
    }

    &::before {
      content: '必須';
      width: 60px;
      height: 30px;
      background-color: $green;
      font-size: 1.6rem;
      font-weight: $bold;
      font-family: $noto-sans;
      color: $white;
      border-radius: 5px;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      @include center;
    }
  }

  &__contact-detail {
    width: 40%;
    @include sp {
      width: 100%;
    }

    &--full {
      height: 220px;
      width: 71%;
      @include sp {
        width: 100%;
      }
    }
  }
  
  &__contact-input {
    width: 100%;
    height: 100%;
    padding: 9px 12px 8px;
    font-size: 1.8rem;
    font-weight: $medium;
    font-family: $noto-sans;
    border: 0.5px solid $gray;
    color: $gray;
    resize: none;
    @include sp {
      font-size: getsp375(32px);
    }

    &:focus {
      outline: none;
    }

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      appearance: none;
      margin: 0;
    }
  }

  &__contact-note {
    position: absolute;
    bottom: 0;
    left: 290px;
    font-size: 1.5rem;
    font-weight: $medium;
    font-family: $noto-sans;
    letter-spacing: $ls20;
    @include sp {
      font-size: getsp375(26px);
      left: 0;
    }
  }

  &__contact-checkbox {
    appearance: none;

    &:checked {
      & ~ {
        span {
          &::before {
            background-color: $green;
            border: 1px solid $green;
          }

          &::after {
            display: block;
          }
        }
      }
    }
  }

  &__contact-button-container {
    width: 380px;
    margin: 0 auto;
    @include sp {
      width: 100%;
    }
  }

  &__footer {
    width: 100%;
    background-color: $green;
    padding-top: 47px;
    @include sp {
      padding-top: getsp375(80px);
    }
  }

  &__footer-wrapper {
    max-width: 962px;
    margin: 0 auto;
    display: flex;
    gap: 37px;
    align-items: center;
    padding: 40px 0 47px;
    @include sp {
      padding: 5%;
      flex-direction: column;
      gap: 20px;
    }
  }

  &__footer-details-container {
    display: flex;
    flex-direction: column;
    gap: 11px;
    width: 425px;
    @include sp {
      width: 100%;
    }
  }

  &__footer-detail {
    display: flex;
  }

  &__footer-detail-label {
    width: 125px;
    font-size: 1.6rem;
    font-weight: $bold;
    font-family: $noto-sans;
    color: $white;
    letter-spacing: $ls50;
    @include sp {
      font-size: getsp375(24px);
      width: 30%;
    }
  }

  &__footer-detail-content {
    width: 299px;
    font-size: 1.5rem;
    font-weight: $regular;
    font-family: $noto-sans;
    color: $white;
    letter-spacing: $ls50;
    @include sp {
      font-size: getsp375(20px);
      width: 70%;
    }
  }

  &__footer-map-container {
    width: 500px;
    height: 190px;
    @include sp {
      width: 100%;
      height: getsp375(200px);
    }
  }

  &__footer-map {
    width: 100%;
    height: 100%;
  }
  
  &__footer-copyright {
    width: 100%;
    height: 40px;
    background-color: $whitef1;
    font-size: 1.2rem;
    font-weight: $regular;
    font-family: $bahnschrift;
    letter-spacing: $ls50;
    line-height: getem(12px, 36px);
    @include center;
    @include sp {
      font-size: getsp375(20px);
    }
  }
}
